.create-team-page .title {
  margin-top: 0.9375rem;
  margin-left: 0.9375rem;
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.create-team-page .team-dec {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.create-team-page .team-dec .team-item {
  position: relative;
  background: linear-gradient(-45deg, #2b2d37, #282b39);
  height: 6.25rem;
  border-radius: 0.3125rem;
  margin-top: 0.9375rem;
  padding-top: 0.9375rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.create-team-page .team-dec .team-item .img {
  position: absolute;
  left: 0.9375rem;
  top: 0;
  width: 2.53125rem;
  height: 2.03125rem;
  background-image: url(/img/TOP.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}
.create-team-page .team-dec .team-item .marginLeft {
  margin-left: 2.8125rem;
}
.create-team-page .team-dec .team-item .top {
  height: 1.875rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  color: #ffffff;
}
.create-team-page .team-dec .team-item .top:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #1a1a1a;
  color: #1a1a1a;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.create-team-page .team-dec .team-item .top .des {
  font-size: 0.875rem;
  font-weight: 400;
}
.create-team-page .team-dec .team-item .top .btn {
  width: 4.0625rem;
  height: 1.25rem;
  line-height: 1.25rem;
  font-size: 0.6875rem;
  border-radius: 0.3125rem;
  background: #00d2ff;
  text-align: center;
  color: #FEFEFF;
}
.create-team-page .team-dec .team-item .bottom {
  display: flex;
  flex: 4 1;
  align-items: center;
  color: #ffffff;
  height: 3.125rem;
}
.create-team-page .team-dec .team-item .bottom .bottom-item:nth-child(3) p {
  margin-left: 1.875rem;
}
.create-team-page .team-dec .team-item .bottom .bottom-item {
  flex: 1 1;
  margin-right: 0.9375rem;
}
.create-team-page .team-dec .team-item .bottom .bottom-item p:nth-child(1) {
  font-size: 1.1875rem;
  font-weight: 600;
  color: #00d2ff;
}
.create-team-page .team-dec .team-item .bottom .bottom-item p:nth-child(1) .little {
  font-size: 0.6875rem;
}
.create-team-page .team-dec .team-item .bottom .bottom-item p:nth-child(2) {
  font-size: 0.6875rem;
  font-weight: 400;
  color: #999999;
}
.create-team-page .input-title {
  margin-top: 0.9375rem;
  margin-left: 0.9375rem;
  font-size: 0.875rem;
  font-weight: 400;
  color: #ffffff;
}
.create-team-page .price-input {
  margin-top: 0.8125rem;
  background: rgba(255, 255, 255, 0);
}
.create-team-page .price-input .password .am-list-line {
  position: relative;
}
.create-team-page .price-input .password .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ddd;
  color: #ddd;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.create-team-page .price-input .am-list-item {
  background: rgba(255, 255, 255, 0);
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.create-team-page .price-input .am-list-item .am-list-line {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  position: relative;
}
.create-team-page .price-input .am-list-item .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ddd;
  color: #ddd;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.create-team-page .price-input .am-list-item .am-list-line input {
  font-size: 0.75rem;
  font-weight: 400;
  color: #666666;
}
.create-team-page .price-input .am-list-item .am-list-line .am-input-extra {
  color: #00D2FF;
}
.create-team-page .des-tip {
  display: flex;
  justify-content: flex-start;
  margin-top: 0.625rem;
  padding-left: 1.5625rem;
  font-size: 0.5625rem;
  color: #ffffff;
}
.create-team-page .des-tip .red {
  color: #FF3333 !important;
}
.create-team-page .des-tip .blue {
  color: #00D2FF !important;
}
.create-team-page .message-error {
  display: flex;
  justify-content: flex-end;
  height: 0.625rem;
  padding-right: 0.9375rem;
  padding-top: 0.625rem;
  font-size: 0.5625rem;
  color: #FF3333;
}
.create-team-page .footerButton {
  margin-top: 3.125rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.am-modal-input input {
  background-color: #272934 !important;
  color: #fff !important;
}
